export default {
	data() {
		return {
			PageCur: 'home', //切换菜单
			toPageCur: '', //上次切换的菜单
			isTop:false, //底部菜单是否在最顶层
		}
	},
	methods: {
		NavChange: function(e) {
			//底部菜单切换
			this.PageCur = e.currentTarget.dataset.cur
		}
	},
	watch:{
		//监听菜单变化
		'PageCur': function(newVal){
			
			var _this=this
			if(newVal=="ble"){
				//如果切换的蓝牙 就把底部菜单设为最顶层 避免蓝牙处弹出提示不方便切换菜单
				_this.isTop=true
			}else{
				_this.isTop=false
			}
			
			//如果上一次切换的菜单是蓝牙，但是本次不是切换不是蓝牙，就关闭蓝牙搜索
			if(newVal!="ble" && _this.toPageCur=="ble"){
				uni.stopBluetoothDevicesDiscovery({
				  success(res) {
					console.log("关闭蓝牙搜索"+res)
				  }
				})
			}
			_this.toPageCur=newVal //赋值上一次切换的菜单
		},
	}
}
